import React from "react";
import {connect} from "react-redux";
import {DetailsLargeSimfreeRaw} from "eshop/modules/core/components/ui/OfferDetails";
import OraModal from "eshop/modules/core/components/ui/Modal";
import {getPriceIsNet} from "eshop/modules/cart/selectors";
import {getContractRole} from "eshop/modules/configurator/selectors/offers";

let popupId=0;
class OfferDetailsPopUp extends React.PureComponent {
    constructor(props) {
        super(props);
        this.state = ({showModal: false});
        this.popupId=this.props.id||("details-popup_trigger"+popupId++);
    }

    getModalProps() {
        return {
            size: "medium",
            showClose: true,
            escapeClose: true,
            clickClose: true,
        };
    }

    setShowModal(modalState) {
        this.setState(state => ({showModal: modalState}));
    }

    onClose() {
        this.setShowModal(false);
    }

    openDetailsModal(event) {
        event.preventDefault();
        this.setShowModal(true);
    }
    preventOfferSwitch(event){
        console.log(event); console.count("event");
        event.stopPropagation();
    }

    getPriceSource(){
        return this.props.entry || this.props.proposition
    }

    render(){
        console.log("OfferDetailsPopUp", this.props);
        return (
            <div
                className="u-inline-block"
                onClick={e=>this.preventOfferSwitch(e)}>
                <a
                    key={"details-popup_trigger"+this.popupId}
                    id={this.popupId+"trigger"}
                    className="u-inline-block u-spacing-right"
                    href="#"
                    onClick={e=>this.openDetailsModal(e)}>
                    {this.props.labelText}
                </a>
                <span className="u-acc-hide">Szczegóły oferty</span>
                <OraModal
                    key={"modal_"+this.popupId}
                    {...this.getModalProps()}
                    id={"modal_"+this.popupId}
                    open={this.state.showModal}
                    onClose={this.onClose.bind(this)}>
                    <DetailsLargeSimfreeRaw
                        key={this.props.proposition.rateplanId + this.props.clientContext}
                        {...this.props}
                        proposition={this.getPriceSource()}/>
                </OraModal>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    showNet: getPriceIsNet(state),
    contractRole: getContractRole(state)[0],
});

const mapDispatchToProps = dispatch => ({
});

OfferDetailsPopUp.defaultProps = {
    header: "Szczególy oferty",
    propositionName: "",
    details: [],
};
export default connect(mapStateToProps, mapDispatchToProps)(OfferDetailsPopUp);
